@import url("https://fonts.googleapis.com/css2?family=Tajawal:wght@200;300;400;500;700;800;900&display=swap");
.submit-disactive {
  opacity: 0.6;
  cursor: no-drop;
}

.input-error {
  border-color: red !important;
}

.height_full {
  height: 100vh !important;
}

@media (min-width: 900px) {
  .hide-plus-12 {
    display: none !important;
  }
}
.seo {
  display: none;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  user-select: none;
}

body {
  font-family: "Tajawal", sans-serif;
  direction: rtl;
  overflow-x: hidden;
  background-color: #141b22;
}

.notifications,
.normal-notifications {
  transition: 1s;
}
.notifications .this_notification,
.normal-notifications .this_notification {
  left: 50%;
  position: fixed;
  bottom: 15px;
  padding: 10px;
  border-radius: 3px;
  transform: translate(-50%);
  font-size: 15px;
  z-index: 3;
}
.notifications .red_notification,
.normal-notifications .red_notification {
  background: #ff3333;
  color: white;
}
.notifications .blue_notification,
.normal-notifications .blue_notification {
  background: #08c;
  color: white;
}
.notifications .green_notification,
.normal-notifications .green_notification {
  background: #1f8742;
  color: white;
}

@media (max-width: 390px) {
  .container {
    width: 100%;
    margin: auto;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 98%;
    margin: auto;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 98%;
    margin: auto;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
    margin: auto;
  }
}
.container {
  display: flex;
  justify-content: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.bg-nav {
  background: #2a2c31;
  width: 100%;
}

nav {
  direction: ltr;
  display: flex;
  background-color: #2a2c31;
  align-items: center;
  justify-content: space-between;
  padding: 10px 18px 10px 11px;
}
nav .left {
  display: flex;
  align-items: center;
  justify-content: center;
}
nav .left .fa-user-circle {
  font-size: 38px;
  position: relative;
  cursor: pointer;
  z-index: 1;
}
nav .left .fa-user-circle::after {
  content: "";
  display: block;
  background: white;
  width: 31px;
  height: 31px;
  border-radius: 100%;
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: -1;
}
nav .left .fa-user-circle:hover::after {
  background-color: #ff9900dc;
}
nav .left .fa-search {
  color: #707070;
  font-size: 22px;
  margin-left: 15px;
  z-index: 0;
  position: relative;
  cursor: pointer;
}
nav .left .fa-search::after {
  content: "";
  background: white;
  display: block;
  position: absolute;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  z-index: -1;
  top: -7px;
  left: -6px;
}
nav .left .fa-search:hover {
  color: #faa300;
}
nav .left .fa-search:hover::after {
  background-color: #faa3000f;
}
nav .center {
  display: flex;
  direction: rtl;
}
nav .center a {
  margin: 0 2px;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
  width: 120px;
  text-align: center;
  padding: 8px 15px;
  border-radius: 4px;
  color: white;
}
nav .center a:hover {
  color: #faa300;
  background: #faa3000f;
}
nav .right .logo {
  height: 35px;
  width: auto;
}
nav .right-md-sm {
  display: none;
}
nav .right-md-sm .fa-bars,
nav .right-md-sm .fa-window-close {
  font-size: 28px;
  color: white;
  cursor: pointer;
  width: 28px;
}
nav .right-md-sm .fa-bars:hover,
nav .right-md-sm .fa-window-close:hover {
  color: #faa300;
}
nav .menu {
  background: #2a2c31;
  position: absolute;
  top: 70px;
  border-radius: 3px;
  z-index: 4;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
}
nav .menu:before {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  top: -33px;
  border: 19px solid transparent;
  border-bottom: 29px solid #2a2c31;
  z-index: -1;
}
nav .menu span,
nav .menu a {
  text-decoration: none;
  margin: 2px 5px;
  font-weight: 700;
  cursor: pointer;
  padding: 8px 15px;
  color: #fff;
  width: 100%;
  text-align: center;
  border-radius: 3px;
}
nav .menu span:hover,
nav .menu a:hover {
  color: #faa300;
  background: #faa3000f;
}
nav .menu .fav {
  padding: 4px;
  border-bottom: 1px solid #ffffff2e;
  width: 100%;
  display: flex;
}
nav .menu ._center {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ffffff2e;
  width: 100%;
  padding-bottom: 5px;
}
nav .menu .bottom {
  display: flex;
  width: 100%;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 4px;
}
nav .menu .bottom .thisusername {
  padding: 8px 15px;
  margin-top: -5px;
}
nav .menu .bottom span {
  padding: 0 8px;
}
nav .menu .bottom .slash {
  cursor: unset;
  padding: 4px 8px;
  margin-bottom: 4px;
  margin-top: -1px;
}
nav .menu .bottom .slash:hover {
  color: white;
  background: #2a2c31;
}

.right-nav {
  background: #2a2c31;
  width: 200px;
  margin-top: 3px;
  height: 95vh;
  display: none;
  transition: 0.5s;
  position: absolute;
  z-index: 2;
}
.right-nav .top {
  border-bottom: 1px solid #ffffff2e;
  margin-bottom: 15px;
}
.right-nav .top a {
  text-decoration: none;
  margin: 0 5px;
  font-weight: 700;
  cursor: pointer;
  padding: 8px 15px;
  border-radius: 4px;
  width: 95%;
  display: flex;
  align-items: center;
  padding-right: 0px;
}
.right-nav .top a .fa-user-circle {
  font-size: 38px;
  position: relative;
  cursor: pointer;
  z-index: 1;
}
.right-nav .top a .fa-user-circle::after {
  content: "";
  display: block;
  background: white;
  width: 31px;
  height: 31px;
  border-radius: 100%;
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: -1;
}
.right-nav .top a i {
  margin-left: 6px;
  color: black;
}
.right-nav .top a span {
  color: white;
}
.right-nav .top a span span.a:hover {
  color: #faa300;
  background: #faa3000f;
}
.right-nav .fav {
  border-bottom: 1px solid #ffffff2e;
  border-top: 1px solid #ffffff2e;
  margin-top: 15px;
  margin-bottom: 15px;
}
.right-nav .center,
.right-nav .fav,
.right-nav .bottom {
  display: flex;
  flex-direction: column;
}
.right-nav .center a,
.right-nav .fav a,
.right-nav .bottom a {
  margin: 2px 5px;
  font-weight: 700;
  cursor: pointer;
  padding: 8px 15px;
  border-radius: 4px;
  color: #fff;
  width: 95%;
  text-decoration: none;
}
.right-nav .center a:hover,
.right-nav .fav a:hover,
.right-nav .bottom a:hover {
  color: #faa300;
  background: #faa3000f;
}

.shearch {
  width: 94%;
  z-index: 3;
  right: 3%;
  position: absolute;
  background: #2a2c31;
  padding: 5px;
  padding-right: 9px;
  border-radius: 3px;
  top: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.shearch form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shearch form .fa-window-close {
  font-size: 25px;
  position: relative;
  cursor: pointer;
}
.shearch form .fa-window-close::after {
  content: "";
  width: 136%;
  height: 134%;
  position: absolute;
  background: white;
  right: -4.3px;
  top: -4.5px;
  z-index: -1;
  border-radius: 3px;
}
.shearch form .fa-window-close:hover {
  color: #faa300;
}
.shearch form input[type=text] {
  border-radius: 3px;
  width: 100%;
  padding: 8px;
  margin-right: 10px;
  border: none;
  outline: none;
  font-weight: bold;
  text-align: center;
  direction: ltr;
  padding-left: 60px;
}
.shearch form .fa-filter {
  position: absolute;
  left: 8px;
  top: 8px;
  font-size: 18px;
  color: #faa300;
}
.shearch form input[type=submit] {
  position: absolute;
  left: 3px;
  background: #faa300;
  padding: 6px 15px;
  border-radius: 3px;
  font-weight: 700;
  font-size: 13px;
  color: #eee;
  border: none;
}

.close-nav {
  margin-right: -230px;
}

.hide {
  display: none !important;
}

.relative {
  position: relative;
}

.open {
  color: #faa300 !important;
}
.open::after {
  background-color: #faa3000f !important;
}

.opacity {
  opacity: 0.3;
  overflow: hidden;
}

.user-icon-bg::after {
  background-color: #ff9900dc !important;
}

.this {
  color: #faa300 !important;
  background-color: #faa3000f !important;
}

@media (max-width: 720px) {
  nav {
    padding: 6px 9px 6px 6px !important;
  }
  nav .center {
    display: none !important;
  }
  nav .left .fa-user-circle {
    display: none !important;
  }
  nav .left .fa-search {
    margin-left: 7px;
  }
  nav .right-md-sm {
    display: block !important;
  }
  nav .menu {
    display: none;
  }

  .right-nav {
    display: block !important;
  }
}
@media (min-width: 1200px) {
  nav {
    max-width: 98%;
    margin: auto;
  }

  nav {
    padding-left: 17px;
    padding-right: 17px;
  }
}
@media (min-width: 1400px) {
  nav {
    max-width: 1320px;
    margin: auto;
  }
}
.register {
  z-index: 2;
  position: fixed;
  background: #2a2c31;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  padding: 10px;
  border-radius: 4px;
  z-index: 5;
  box-shadow: rgba(0, 0, 0, 0.25) -4px -4px 15px, rgba(0, 0, 0, 0.12) 0px -12px 20px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) -5px 2px 13px, rgba(0, 0, 0, 0.09) 0px -3px 20px;
}
.register form {
  display: flex;
  flex-direction: column;
}
.register form label {
  margin-bottom: 3px;
  direction: initial;
  font-weight: bold;
  font-size: 13px;
  padding-top: 10px;
  margin-left: 5px;
  color: azure;
}
.register form input {
  border: 2px solid transparent;
  padding: 4px;
  outline: none;
  background: white;
  direction: initial;
  padding-left: 10px;
  font-weight: bold;
  color: #000000b8;
}
.register form #submit {
  background: #f9a303;
  color: white;
  margin-top: 10px;
}

.login {
  padding-top: 1px;
  box-shadow: rgba(0, 0, 0, 0.25) -4px -4px 15px, rgba(0, 0, 0, 0.12) 0px -12px 20px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) -5px 2px 13px, rgba(0, 0, 0, 0.09) 0px -3px 20px;
}

.logout {
  color: #ff0000b8 !important;
  background: #0000000f !important;
  padding: 5px !important;
  margin-top: -2px !important;
}

.left-slid {
  display: none;
  width: 29%;
  margin-right: 1%;
}
.left-slid .categorys {
  color: white;
  background-color: #2a2c3169;
}
.left-slid .categorys .title {
  background: #292d33;
  display: block;
  padding: 10px 12px;
  font-weight: 700;
  margin-bottom: 6px;
}
.left-slid .categorys .all {
  margin: auto;
}
.left-slid .categorys .all a {
  text-decoration: none;
  color: white;
  display: inline-block;
  padding: 10px;
  background: #292d33;
  width: 32.3333333333%;
  text-align: center;
  border-bottom: 3.5px solid #1d2228;
}
.left-slid .categorys .all a:hover {
  color: #faa300;
}
.left-slid .your-new {
  color: white;
  background-color: #2a2c3169;
  margin-top: 15px;
}
.left-slid .your-new > .title {
  width: 100% !important;
  display: flex;
  justify-content: space-between;
  background: #292d33;
  padding: 5px;
  border: 2px solid #1d2228;
  padding: 10px 12px;
  font-weight: bold;
}
.left-slid .your-new .episode span {
  background: #d98e08;
  color: #1d2228;
  font-size: 13px;
  padding: 1px;
  border-radius: 3px;
  padding-left: 3px;
  padding-right: 3px;
}
.left-slid .your-new .anime {
  text-decoration: none;
  color: white;
  width: 100% !important;
  display: flex;
  justify-content: space-between;
  background: #292d33;
  padding: 5px;
  border: 2px solid #1d2228;
  position: relative;
}
.left-slid .your-new .anime .title {
  margin-right: 20px;
}
.left-slid .your-new .anime .title::after {
  content: "";
  right: 5px;
  display: block;
  width: 10px;
  height: 10px;
  background: #f44336;
  position: absolute;
  top: 9px;
  border-radius: 7px;
}

@media (max-width: 900px) {
  .left-slid .categorys .all a {
    width: 49% !important;
  }
}
@media (min-width: 720px) {
  .left-slid {
    display: block !important;
  }
}
.content {
  margin: auto;
  margin-bottom: 10px;
  background: #2a2c3169;
  border-radius: 5px;
}

@media (min-width: 720px) {
  .content {
    width: 69%;
    margin: 0px;
  }
}
@media (max-width: 719px) {
  .content {
    max-width: 97%;
    width: 97%;
  }
}
.container.full {
  margin-top: 15px;
}
.container.full .content > .title {
  color: white;
  background: #f0f8ff08;
  padding: 10px;
  font-size: 18px;
  font-weight: 700;
}
.container.full .content > .title a {
  color: #f7a303;
  text-decoration: none;
}
.container.full .content .video {
  position: relative;
}
.container.full .content .video iframe {
  width: 100%;
  height: 55vw;
}
.container.full .content .video .fa-compress {
  color: #fff;
  font-size: 25px;
  margin-left: 15px;
  bottom: 11px;
  position: absolute;
  right: 23px;
}
.container.full .content .video .control {
  background: #f0f8ff08;
  padding: 8px;
  margin-top: -4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
}
.container.full .content .video .control .all-ep {
  display: flex;
  align-items: center;
}
.container.full .content .video .control .all-ep .fa-expand {
  color: white;
  font-size: 20px;
  margin-left: 15px;
}
.container.full .content .video .control .all-ep a {
  background: #d98e08;
  padding: 3px;
  text-decoration: none;
  color: white;
  border-radius: 2px;
  font-size: 15px;
}
.container.full .content .video .control .all-ep a i {
  margin-right: 4px;
}
.container.full .content .video .control select {
  outline: none;
  border: none;
  text-align: center;
  background: #333941;
  color: white;
  border-radius: 2px;
}

@media (min-width: 720px) {
  iframe {
    height: 38vw !important;
  }
}
@media (min-width: 1400px) {
  iframe {
    height: 520px !important;
  }
}